    /* height: 790px; */
    position: relative;
    z-index: 1;
    -webkit-animation: beat .80s infinite alternate;
            animation: beat .80s infinite alternate;
	 -webkit-transform-origin: center;
	     -ms-transform-origin: center;
	         transform-origin: center;
}
#boysquig,
#girlsquig {
  width: 85%;
  -webkit-animation: beat .80s infinite alternate;
          animation: beat .80s infinite alternate;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
}
@-webkit-keyframes beat{
	to { -webkit-transform: scale(1.03); transform: scale(1.03); }
}
@keyframes beat{
	to { -webkit-transform: scale(1.03); transform: scale(1.03); }
}

